<template>
  <div style="min-width: 1150px">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{path:'/deviceUser'}">终端用户</el-breadcrumb-item>
      <el-breadcrumb-item>量子密钥管控</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <div id="pie"></div>
      <h2>量子密钥操作：</h2>
      <div style="display: flex;justify-content: flex-start;padding: 20px 0">
        <div class="content">
          <img src="../assets/image/06.png" alt="">
          <el-button type="text" @click="getKey">分配额度</el-button>
        </div>
        <div class="content">
          <img src="../assets/image/02.png" alt="">
          <el-button type="text" @click="exportKey">导出</el-button>
        </div>
        <div class="content">
          <img src="../assets/image/03.png" alt="">
          <el-button type="text" @click="recallKey">撤回</el-button>
        </div>
        <div class="content">
          <img src="../assets/image/05.png" alt="">
          <el-button type="text" @click="destoryKey">销毁</el-button>
        </div>
      </div>
    </el-card>
    <el-dialog :title="title" :visible.sync="keyDialogVisible" width="520px" @close="keyDialogClosed">
      <el-form :model="keyForm" :rules="keyFormRules" ref="keyFormRef" label-width="80px">
        <el-form-item label="密钥ID" prop="keyId">
          <el-select style="width: 100%" v-model="keyForm.keyId" placeholder="请选择" @visible-change="oneClick($event)">
            <el-option
              v-for="(item,index) in options"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="keyDialogVisible = false">取 消</el-button>
        <el-button type="primary" v-preventReClick="1000" @click="keyEnsure">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import * as echarts from 'echarts';

  export default {
    data() {
      return {
        options: [],
        title: '',
        deviceName: this.$route.query.deviceName,
        keyDialogVisible: false,
        keyForm: {
          keyId: '',
        },
        keyFormRules: {
          keyId: [{required: true, message: '请选择', trigger: 'change'}],
        }
      }
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      } else {
        return next('/404')
      }
    },
    created() {
      this.getUserList();
    },
    methods: {
      async getUserList() {
        const {data: res} = await this.$http.get(`keyInfo/getKeyUsedInfo?ts=${new Date().getTime()}`, {params: {'applicant': this.deviceName}});
        if (res.code !== 0) return this.$message.error((res.msg !== '' && res.msg !== null) ? res.msg : '获取凭据列表失败！');
        this.chart(res.data.totalNum, res.data.usedNum)
      },
      recallKey() {
        this.keyDialogVisible = true;
        this.title = '撤回';
      },
      destoryKey() {
        this.keyDialogVisible = true;
        this.title = '销毁';
      },
      keyEnsure() {
        this.$refs.keyFormRef.validate(async valid => {
          if (!valid) return;
          let url = '';
          if (this.title === '撤回') {
            url = 'keyInfo/recallKey'
          } else {
            url = 'keyInfo/destroyKey'//销毁
          }
          const {data: res} = await this.$http.post(url, {'keyId': this.keyForm.keyId});
          if (res.code !== 0) return this.$message.error((res.msg !== '' && res.msg !== null) ? res.msg : this.title + '失败！');
          this.$message.success(this.title + '成功！');
          this.keyDialogVisible = false;
        })
      },
      keyDialogClosed() {
        this.$refs.keyFormRef.resetFields();
      },
      oneClick(even) {
        if (even === true) {//下拉框显示
          this.getSelectValue();
        }else {
          this.options = [];
        }
      },
      async getSelectValue() {
        const {data: res} = await this.$http.get('keyInfo/getApplicantKeyId', {params: {'applicant': this.deviceName}});
        if (res.code !== 0) return this.$message.error(res.msg !== null && res.msg !== '' ? res.msg : '获取密钥ID失败！');
        this.options = res.data;
      },
      async getKey() {
        const confirmResult = await this.$prompt('请输入量子密钥总量', '分配额度', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^[1-9]\d*$/,
          inputType: 'text',
          inputErrorMessage: '请输入正整数'
        }).catch(err => err);
        if (confirmResult.action !== 'confirm') {
          return
        }
        const {data: res} = await this.$http.get(`keyInfo/addKey?ts=${new Date().getTime()}`, {
          params: {
            'applicant': this.deviceName,
            'keyNum': confirmResult.value
          }
        });
        if (res.code !== 0) return this.$message.error((res.msg !== '' && res.msg !== null) ? res.msg : '分配额度失败！');
        this.$message.success('分配额度成功！');
        this.getUserList();
      },
      exportKey() {
        let saveUrl = 'keyInfo/exportKeyInfos';
        let reqData = {'applicant': this.deviceName};
        this.$http.post(`${saveUrl}`, reqData, {responseType: 'blob'}).then(res => {
          const filename = decodeURI(res.headers['content-disposition'].split(';')[1].split('=')[1]);
          this.Global.downloadFile(res.data, filename);
        })
      },
      chart(total, use) {
        let myChart = echarts.init(document.getElementById('pie'));
        myChart.setOption({
          title: [{text: '量子密钥使用量', left: 'center'}, {
            subtext: `量子密钥总量:${total}     量子密钥使用数量:${use}`,
            left: '50%',
            top: '85%',
            textAlign: 'center'
          }],
          series: [
            {
              type: 'gauge',
              radius: '50%',
              startAngle: 90,
              endAngle: -270,
              center: ['50%', '50%'],
              itemStyle: {
                color: '#409EFF',
              },
              pointer: {
                show: false
              },
              progress: {
                show: true,
                overlap: false,
                roundCap: true,
                clip: false,
                itemStyle: {
                  borderWidth: 1,
                  borderColor: '#409EFF'
                }
              },
              axisLine: {
                lineStyle: {
                  width: 15
                }
              },
              splitLine: {
                show: false,
                distance: 0,
                length: 10
              },
              axisTick: {//刻度样式
                show: false
              },
              axisLabel: {
                show: false,
                distance: 50
              },
              data: [
                {value: ((use / total) * 100).toFixed(2)}
              ],
              detail: {////仪表盘详情，用于显示数据
                valueAnimation: true,
                fontSize: 18,
                color: 'auto',
                formatter: '{value}%',
                offsetCenter: ['0%', '0%'],
              }
            }
          ]
        });
        window.addEventListener("resize", function () {
          myChart.resize();
        });
      }
    }
  }

</script>
<style lang="less" scoped>
  #pie {
    width: 300px;
    height: 300px;
  }

  h2 {
    border-bottom: 1px solid #eee;
    padding: 20px 0;
  }

  .content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 100px;
    margin-right: 30px;
    background-color: #ecf5ff;
    border-radius: 6px;
    img {
      width: 42px;
      height: 42px;
    }
    .el-button--text {
      color: #409EFF;
      background: 0 0;
      font-size: 18px;
      padding: 0 10px;
    }
  }
</style>

